<template>
  <div class="demo">
    <h2>短信发送</h2>
      <div  class="phone-box">
        <van-form @submit="onSubmit">
          <van-field
            v-model="phone"
            name="手机号"
            label="手机号"
            type="tel"
            placeholder="请输入手机号"
            maxlength="11"
            :rules="[{ required: true, message: '请输入手机号' }]"
          />
          <div style="margin: 16px;">
            <van-button round block type="info" native-type="submit" >发送短信</van-button>
          </div>
        </van-form>
        
      </div>
  </div>
</template>

<script>
  export default{
    name:'demo',
    data(){
      return{
        phone:'15026753453',
        rules: {
          phone: [
            { required: true, message: '请输入手机号', trigger: 'blur' },
          ],
        },
      }
    },
  
    methods:{
      smsPhone(phoneNum){
        console.log('phoneNum',phoneNum);

        this.$axios({
          method: 'post',
          url: 'aliyunSmsService/sendVerificationCode',
          data: {
            phone: phoneNum,
            code: "164714"
          }
        }).then(function (response) {
          console.log(response);
        }).catch(function (error) {
          console.log(error);
        });
      },
      onSubmit(){
        // console.log('发送短信');
        this.smsPhone(this.phone);
        // Toast('短信发送成功');
      }

    },
  }
</script>

<style scoped>
  .demo {padding-top: 15px;width: 100%;}
  .demo h2 {font-size: 16px;text-align: center;font-weight: bold;margin:10px 0 30px 0;}
  .phone-box {display:flex;justify-content: space-between;align-items: center;width: 100%;}
  .van-form {width: 100%;}
  
</style>


