<template>
  <div class="business">
    <h2>办理业务</h2>
    <p class="text">感谢您的参与</p>
  </div>
</template>

<script>
  import { Toast,Dialog } from 'vant';
  export default{
    components: {
      [Dialog.Component.name]: Dialog.Component,
    },
    data(){
      return{

      }
    },
    mounted(){
      this.openPop();
    },
    methods:{
      openPop(){
        Dialog.confirm({
          title: '业务办理',
          message: '是否确认订购此业务',
        })
          .then(() => {
            // console.log('同意');
            window.location.href='sms:10016&body=同意办理';
            // on confirm
          })
          .catch(() => {
            Toast('已取消发送');
            // on cancel
          });
      }
    }
  }
</script>

<style scoped>
  .business {padding-top: 15px;}
  h2 {font-size: 16px;text-align: center;font-weight: bold;margin:10px 0 25px 0;}
  .text {text-align: center;margin-top: 30px;}

  
</style>


