import Vue from 'vue'
import Router from 'vue-router'
import PhoneList from '../../pages/phoneList.vue'
import business from '../../pages/business.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/phonelist',
      name: 'phonelist',
      component: PhoneList
    },
    {
      path: '/',
      name: 'business',
      component: business
    }
  ]
})



